.Maincont {
  margin: auto;
  min-height: 80vh;
}
.flexing {
  display: flex;
}
@media only screen and (max-width: 1024px) {
  .Maincont {
    height: auto;
    padding-bottom: 100px;
  }
}

@media only screen and (max-width: 768px) {
  .Maincont {
    width: 100%;
    height: auto;
    padding-bottom: 100px;
  }
  .flexing {
    flex-direction: column;
    width: 100%;
  }
  .contactForm {
    width: 100%;
    margin: auto !important;
  }
  .contactInfos {
    width: 100%;
    margin: 50px 10% !important;
  }
}
.contactForm {
  width: 50%;
  /* margin:auto; */
  margin-left: 100px;
  /* background-color:red; */
}
.contactInfos {
  width: 50%;
  /* margin:auto; */
  margin: 20px;
  /* background-color:red; */
}
.contactForm > div {
  width: 100%;
  display: flex;
}
.contactForm > div > label {
  color: #4c4c4c;
  margin-bottom: 5px;
  margin-left: 10px;
  font-size: 16px;
  font-weight: 900;
}

.contactForm > input,
.contactForm > textarea {
  display: block;
  width: 100%;
  height: 38px;
  padding: 8px 12px;
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 1.42857143;

  background-color: #fff;
  border: 1px solid #ccc;
  background-color: rgba(27, 27, 27, 0.91);
  max-width: 650px;
  border-style: none;
  border-width: 1px;
  border-color: hsla(0, 0%, 100%, 0);
  border-radius: 10px;
  color: #fff;
}

.contactForm > textarea {
  height: 200px;
}

.form_submit {
  border: none;
  min-height: 48px;
  min-width: 118px;
  margin-top: 10px;
  border-radius: 12px;
  color: #c4c4c4;
  font-size: 18px;
  font-weight: 500;
  background-color: rgba(27, 27, 27, 0.91);
}

.phone_email {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-left: 30%;
}
.blanks {
  height: 60%;
}
.phone_email > div > h3 {
  color: #4c4c4c;
}

.phoneheref {
  text-decoration: none;
  color: #4c4c4c;
}
