.about_Wrapper_parent {
  padding-top: 10px;
  width: 100%;
  margin: auto;
  min-height: 70vh;
  padding-bottom: 100px;
}

.about_Wrapper {
  width: 100%;
}
.tobeCenter {
  width: 90%;
  margin: auto;
  /* background-color:brown; */
}
.about_heading {
  width: 20%;
  min-width: 200px;

  margin: 10px auto;
  padding-bottom: 5px;
  /* border-bottom: 5px solid #c4c4c4; */
  border-bottom: 5px solid#4c4c4c;
}
.about_heading > h1 {
  /* color:#c4c4c4; */
  color: #4c4c4c;
}
.imgContainerParent {
  display: flex;
  width: 90%;
  margin: auto;
  gap: 5%;
}
.aboutmecontainer {
  width: 50%;
}

.imgContainer {
  width: 50%;
}
.aboutme {
  font-size: 30px;
  font-family: cursive;
  line-height: 2;
  text-align: left;
}

@media only screen and (min-width: 540px) {
}

@media only screen and (max-width: 1024px) {
  .about_Wrapper_parent {
    width: 100%;
    margin: auto;
    height: auto;
    padding-bottom: 100px;
  }
  .imgContainerParent {
    display: flex;
    flex-direction: column;
    width: 90%;
    margin: auto;
    gap: 5%;
  }
  .aboutmecontainer {
    width: 80%;
    margin: auto;
  }

  .imgContainer {
    width: 80%;
    margin: auto;
  }
}
