@media only screen and (max-width: 768px) {
  .wrappertext {
    padding-top: 50px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    gap: 20px;
    height: 70vh;
  }
  .wrappedtext {
    font-size: 30px;
    /* color:#c4c4c4; */
    color: #4c4c4c;
    width: 60%;
    margin: 0% auto;
    height: 50%;
  }
  .imageWrapper {
    width: 100%;
    margin: auto;

    /* margin-left:20%; */
  }
}

@media only screen and (min-width: 768px) {
  .wrappertext {
    padding-top: 50px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 70vh;
  }
  .wrappedtext {
    width: 100%;
    font-size: 35px;
    color: #4c4c4c;
    margin: auto;
    height: 100%;
    display: grid;
    grid-template-rows: 50% 20%;
    grid-template-areas:
      "text"
      "resume";
  }
  .wrappedtext > div:nth-child(1) {
    grid-area: "text";
  }
  .wrappedtext > div:nth-child(2) {
    margin: auto;
    grid-area: "resume";
  }

  .imageWrapper {
    width: 100%;
    margin: auto;
  }
}

@media only screen and (min-width: 1024px) {
  .wrappertext {
    padding-top: 200px;
    height: 70vh;
    display: flex;
    flex-direction: row-reverse;
  }

  .wrappedtext {
    font-size: 35px;
    color: #4c4c4c;
    width: 50%;
    margin: auto 5%;
    height: 100%;
    display: grid;
    grid-template-rows: 50% 20%;
    grid-template-areas:
      "text"
      "resume";
  }
  .wrappedtext > div:nth-child(1) {
    grid-area: "text";
  }
  .wrappedtext > div:nth-child(2) {
    margin: auto;
    grid-area: "resume";
  }

  .imageWrapper {
    width: 50%;
  }
}

@media only screen and (min-width: 1200px) {
  .wrappertext {
    padding-top: 200px;
    height: 70vh;
    display: flex;
    flex-direction: row-reverse;
    /* align-items: center; */
  }

  .wrappedtext {
    font-size: 50px;
    color: #4c4c4c;
    width: 50%;
    margin: auto 5%;
    height: 100%;
    display: grid;
    grid-template-rows: 50% 20%;
    grid-template-areas:
      "text"
      "resume";
  }
  .wrappedtext > div:nth-child(1) {
    margin-top: 10%;
    grid-area: "text";
  }
  .wrappedtext > div:nth-child(2) {
    width: 30%;
    margin: auto;
    grid-area: "resume";
  }

  .imageWrapper {
    width: 50%;
    height: 100%;
    /* background-color: aqua; */
  }
}
