.App {
  text-align: center;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, rgb(151, 248, 226) 0%, #acddfe6c 100%);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

button:hover {
  cursor: pointer;
}

input:focus,
textarea:focus {
  outline: none;
  box-shadow: 0px 0px 1px rgb(151, 248, 226);
  transform: scale(1.025);
}
button {
  color: #c4c4c4;
}
button:focus {
  color: rgb(151, 248, 226);
}

img:hover,
button:hover {
  -ms-transform: scale(1.1); /* IE 9 */
  -webkit-transform: scale(1.1); /* Safari 3-8 */
  transform: scale(0.9);
  animation: App-logo-spin infinite 20s linear;
}
